import React from 'react';
import Grid from '@material-ui/core/Grid';
import * as S from './styles';
import BatteryIcon from '../../Icon/BatteryIcon/BatteryIcon';
import InfoItem from './InfoItem';

type UnitPerformanceProps = {
    isCharging: boolean;
    soc: number;
    soh: number;
    capacity: number;
    current: number;
    voltage: number;
};

const UnitPerformance: React.FC<UnitPerformanceProps> = (props) => {
    return (
        <S.InfoPaper>
            <Grid container spacing={0}>
                <Grid item xs={12}>
                    <S.Header>Unit Performance</S.Header>
                </Grid>
                <InfoItem itemKey="Capacity">
                    <S.Value>{props.capacity} Ah</S.Value>
                </InfoItem>
                <InfoItem itemKey="Current">
                    <S.Value>{props.current} Ah</S.Value>
                </InfoItem>
                <InfoItem itemKey="Voltage">
                    <S.Value>{props.voltage} Ah</S.Value>
                </InfoItem>
                <InfoItem itemKey="SoC">
                    <S.Value>
                        <Grid container spacing={0}>
                            <Grid item xs={5}>
                                <BatteryIcon soc={props.soc} size={60} />
                            </Grid>
                            <Grid item xs={7}>
                                <S.Value>{Math.round(props.soc * 100)}%</S.Value>
                            </Grid>
                        </Grid>
                    </S.Value>
                </InfoItem>
                <InfoItem itemKey="SoH">
                    <S.Value>
                        <Grid container spacing={0}>
                            <Grid item xs={5}>
                                <BatteryIcon soc={props.soh} size={60} />
                            </Grid>
                            <Grid item xs={7}>
                                <S.Value>{Math.round(props.soh * 100)}%</S.Value>
                            </Grid>
                        </Grid>
                    </S.Value>
                </InfoItem>
            </Grid>
        </S.InfoPaper>
    );
}

export default UnitPerformance;