import React, { useEffect } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../../stores/';
import { fetchUnit } from '../../../stores/unit';
import Grid from '@material-ui/core/Grid';
import Alert from '@material-ui/lab/Alert';
import UnitInfo from '../../../components/BatteryUnit/Unit/UnitInfo';
import UnitPerformance from '../../../components/BatteryUnit/Unit/UnitPerformance';
import CustomerInfo from '../../../components/BatteryUnit/Unit/CustomerInfo';
import LocationMap from '../../../components/BatteryUnit/Unit/LocationMap';

type UnitProps = {} & RouteComponentProps<{id: string}>;

const Unit: React.FC<UnitProps> = props => {
    const dispatch = useDispatch();
    const unit = useSelector((state: RootState) => state.unit.unit);
    const id: string = props.match.params.id;

    useEffect(() => {
        console.log('fetching unit data');
        dispatch(fetchUnit(id));
    }, []);

    let unitDetail = null;
    
    if( unit.id === '') {
        unitDetail = (<p>loading...</p>);
    } else {
        let alertMessage = null;
        if (unit.requiredAction !== undefined) {
            alertMessage = (<Alert severity="error">{unit.requiredAction}</Alert>);
        }
        unitDetail = (
            <Grid container spacing={1}>
                <Grid item xs={12} sm={12}>
                    {alertMessage}
                </Grid>
                <Grid item xs={12} sm={6}>
                    <UnitInfo
                        requiredAction={unit.requiredAction}
                        id={unit.id}
                        modelNumber={unit.profile.modelNumber}
                        productTypeName={unit.profile.productTypeName}
                        isPowerOn={unit.status.isPowerOn}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <UnitPerformance
                        isCharging={unit.status.isCharging}
                        capacity={unit.status.capacity}
                        current={unit.status.current}
                        voltage={unit.status.voltage}
                        soc={unit.status.soc}
                        soh={unit.status.soh}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <CustomerInfo
                        companyName="Komatsu"
                        industry="Heavy Machinery"
                        personnelName="Taro Yamada"
                        email="tyamada@hogehoge.co.jp"
                        phoneNumber="06-2345-1234"
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <LocationMap
                        location={unit.profile.location}
                        id={unit.id}
                    />
                </Grid>
            </Grid>
        );
    }

    return (
        <>
        {unitDetail}
        </>
    );
}

export default Unit;