import React from 'react';
import Grid from '@material-ui/core/Grid';
import * as S from './styles';
import InfoItem from './InfoItem';

type UnitInfoProps = {
    requiredAction: string | undefined;
    id: string;
    modelNumber: string;
    productTypeName: string;
    isPowerOn: boolean;
};

const UnitInfo: React.FC<UnitInfoProps> = (props) => {
    return (
        <S.InfoPaper>
            <Grid container spacing={0}>
                <Grid item xs={12}>
                    <S.Header>Unit Info</S.Header>
                </Grid>
                <InfoItem itemKey="ID">
                    <S.Value>{props.id}</S.Value>
                </InfoItem>
                <InfoItem itemKey="Model Numbe">
                    <S.Value>{props.modelNumber}</S.Value>
                </InfoItem>
                <InfoItem itemKey="Product Type">
                    <S.Value>{props.productTypeName}</S.Value>
                </InfoItem>
                <InfoItem itemKey="Battery Status">
                    <S.Status status={props.isPowerOn}>{props.isPowerOn? 'ON' : 'Off' }</S.Status>
                </InfoItem>
            </Grid>
        </S.InfoPaper>
    );
}

export default UnitInfo;