import React from 'react';
import Grid from '@material-ui/core/Grid';
import GoogleMapReact from 'google-map-react';
import * as S from './styles';
import { GeoLocation } from '../../../types/unit';

type LocationMapProps = {
    location: GeoLocation;
    id: string;
};

type Props = {
    text: string;
    lat: number;
    lng: number;
}

const AnyReactComponent: React.FC<Props> = (props) => <div>{props.text}</div>

const LocationMap: React.FC<LocationMapProps> = (props) => {
    return (
        <S.InfoPaper>
            <Grid container spacing={0}>
                <Grid item xs={12}>
                    <S.Header>Location</S.Header>
                </Grid>
                <Grid item xs={12} style={{ height: '50vh' }}>
                    <GoogleMapReact
                        bootstrapURLKeys={{ key: 'AIzaSyDGVrzNW2FhUWk0-ompNE_KNcJdUT9_z4c' }}
                        defaultCenter={{lat: props.location.latitude, lng: props.location.longitude}}
                        defaultZoom={11}
                    >
                        <AnyReactComponent
                            lat={props.location.latitude}
                            lng={props.location.longitude}
                            text={props.id}
                        />
                    </GoogleMapReact>
               </Grid>
            </Grid>
        </S.InfoPaper>
    );
}

export default LocationMap;