import { createSlice } from '@reduxjs/toolkit';
import { db } from '../firebase';
import { Dispatch } from 'redux';
import { UnitSummary } from '../types/unit';
import { setProductTypeName, setRequiredAction } from '../services/format/unit';

interface UnitsState {
    units: UnitSummary[],
}

const initialState: UnitsState = {
    units: []
};

const unitsSlice = createSlice({
    name: 'units',
    initialState,
    reducers: {
        setUnits: (state, action) => {state.units = action.payload},
        // setUnit: (state, action) => {state.units.push(action.payload)},
    }
});

export default unitsSlice.reducer;
export const { setUnits } = unitsSlice.actions;

export const initUnits = () => async (dispatch: Dispatch) => {
    try {
        db.collection('units').get().then(querySnapShot => {
            let unitsList: UnitSummary[] = [];
            querySnapShot.forEach(doc => {
                unitsList.push({
                    id: doc.id,
                    requiredAction: setRequiredAction(doc.data().errorCode),
                    profile: {
                    productTypeName: setProductTypeName(doc.data().profile.productType),
                    modelNumber: doc.data().profile.modelNumber,
                    location: doc.data().profile.location,
                    },
                    isCharging: doc.data().status.isCharging,
                    isPowerOn: doc.data().status.isPowerOn,
                    soc: doc.data().status.soc,
                });
            }
            );
            dispatch(setUnits(unitsList));
        });
    } catch (err) {
        console.log(err);
    }
}