import React from 'react';
import {
  StylesProvider,
  ThemeProvider as MaterialThemeProvider
} from '@material-ui/styles';
import {
  ThemeProvider as StyledThemeProvider
} from 'styled-components';
import { Route, withRouter, Switch } from 'react-router-dom';
import theme from './theme';
import Layout from './components/Layout/Layout';
import Units from './containers/BatteryUnit/Units/Units';
import Unit from './containers/BatteryUnit/Unit/Unit';
import {Login} from './test/test';
//import {Login} from './containers/API/Login';

const App: React.FC = () => {
  const routes = (
    <Switch>
      <Route exact path='/units' component={Login}/>
      <Route path='/units/:id' component={Unit}/>
    </Switch>
  );
  return (
    <StylesProvider injectFirst>
      <MaterialThemeProvider theme={theme}>
        <StyledThemeProvider theme={theme}>
          <Layout>
            kitao test
            {routes}
          </Layout>
        </StyledThemeProvider>
      </MaterialThemeProvider>
    </StylesProvider>
  );
}

export default withRouter(App);
