import { createSlice } from '@reduxjs/toolkit';
import { db } from '../firebase';
import { Dispatch } from 'redux';
import { UnitDetail } from '../types/unit';
import { setProductTypeName, setRequiredAction } from '../services/format/unit';

interface UnitState {
    unit: UnitDetail,
}

const initialState: UnitState = {
    unit: {
        id: '',
        errorCode: 0,
        requiredAction: undefined,
        profile: {
            modelNumber: '',
            productTypeName: '',
            location: {
                latitude: 0,
                longitude: 0
            },
        },
        status: {
            isCharging: false,
            isPowerOn: false,
            soc: 0,
            soh: 0,
            capacity: 0,
            current: 0,
            voltage: 0,
        },
        timestamps: {
            registeredAt: new Date(0),
            lastUpdatedAt: new Date(0),
        },
        customerId: '',
    }
};

const unitSlice = createSlice({
    name: 'unit',
    initialState,
    reducers: {
        setUnit: (state, action) => {state.unit = action.payload},
    }
});

export default unitSlice.reducer;
export const { setUnit } = unitSlice.actions;

// define fetchUnit
// take id as a parameter, and fetch the corresponding unit data from FireStore
// if it doesn't exist in server, return error

export const fetchUnit = (id: string) => async ( dispatch: Dispatch ) => {
    try{
        db.collection('units').doc(id).get().then(doc => {
            if (doc.exists) {
                const unit : UnitDetail = {
                    id: doc.id,
                    errorCode: doc.data()?.errorCode,
                    requiredAction: setRequiredAction(doc.data()?.errorCode),
                    profile: {
                        modelNumber: doc.data()?.profile.modelNumber,
                        productTypeName: setProductTypeName(doc.data()?.profile.productType),
                        location: doc.data()?.profile.location,
                    },
                    status: doc.data()?.status,
                    timestamps: doc.data()?.timestamps,
                    customerId: doc.data()?.customerId,
                }
                dispatch(setUnit(unit));
            } else {
                console.log("no such doc");
            }
        });
    } catch (err) {
        console.log(err);
    }
}