export const setRequiredAction = (errorCode: number) => {
    switch (errorCode) {
        case 0:
            return undefined;
        case 1:
            return 'To be collected';
        case 2:
            return 'Please contact customer';
        case 3:
            return 'other error';
        default:
            return 'some errors';
    }
}

export const setProductTypeName = (productType: number) => {
    switch (productType) {
        case 0:
            return 'forklift';
        case 1:
            return 'cold storage';
        case 2:
            return 'heavy machinary';
        default:
            return 'Product Type undefined';
    }
}